body
    {
        background:#f2f2f2;
    }

    .payment_echoue
	{
		border:1px solid #f2f2f2;
		height:280px;
        border-radius:20px;
        background:#fff;
	}
   .payment_header_echoue
   {
       background:rgb(221, 30, 30);
	     padding:20px;
       border-radius:20px 20px 0px 0px;
	   
   }
   
   .check_echoue
   {
	   margin:0px auto;
	   width:50px;
	   height:50px;
	   border-radius:100%;
	   background:#fff;
	   text-align:center;
   }
   
   .check_echoue i
   {
	   vertical-align:middle;
	   line-height:50px;
	   font-size:30px;
   }

    .content_echoue 
    {
        text-align:center;
    }

    .content_echoue  h1
    {
        font-size:25px;
        padding-top:25px;
    }

    .content_echoue a
    {
        width:200px;
        height:35px;
        color:#fff;
        border-radius:30px;
        padding:5px 10px;
        background:rgb(221, 30, 30);
        transition:all ease-in-out 0.3s;
    }

    .content_echoue a:hover
    {
        text-decoration:none;
        background:#000;
    }